body{
  /*background-image: linear-gradient(to bottom right, rgba(85, 76, 76, 0.486), rgb(241, 241, 95),rgb(240, 141, 75));*/
  background: linear-gradient(-45deg, #ffebe5, #ffdeea, #dcf6ff, #d1fff4,#ffd9ce, #ffe0ec, #dbf5ff, #e0fff8);
  background-repeat: no-repeat;
  /*background-size: contain;*/
  background-size: 400% 500%;

  /*background-attachment: fixed; */  /* very imp to fill all the back groud with gradiant color*/
  /*animation: background_grad 2s infinite;*/
  animation: background_grad 15s ease infinite;
  transition: background-image 2s;
}

/*
@keyframes background_grad{
  from {    background-image: linear-gradient(to bottom right, rgba(85, 76, 76, 0.486), rgb(241, 241, 95),rgb(240, 141, 75));
  }
  to {    background-image: linear-gradient(to bottom right, rgba(187, 150, 150, 0.486), rgb(163, 163, 68),rgb(218, 103, 27));
  }
}*/

@keyframes background_grad{
0% {
  background-position: 0% 50%;
}
50% {
  background-position: 100% 50%;
}
100% {
  background-position: 0% 50%;
}
}

#main_heading{
  text-align: center;
}
#main_heading{
  font-weight: bold;
  font-size: 30px;
  color: white;
  margin-top: 30px;
  margin-bottom: 30px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  /*adding gradient to text but we want to add when we hover it and also want to add transition*/
  /*
  background: -webkit-linear-gradient(rgb(102, 100, 100), rgb(211, 126, 126));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;*/
  transition-property: font-size;
  transition-duration: 1s;
  transition-delay: 0s;
  transition-timing-function: ease-in-out;
  /*for giving animations to it*/
  background: -webkit-linear-gradient(-45deg, #38302d, #41373a, #31393b, #343b3a);
  background-size: 400%,400%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: background_grad_text 5s ease infinite;
}


#main_heading:hover{
  font-size: 55px;
  background: -webkit-linear-gradient(-45deg, #4c4644, #332e30, #303536, #252928);
  background-size: 400%,400%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: background_grad_text 5s ease infinite;

}

/*
@keyframes background_grad_text{
  from {      background: -webkit-linear-gradient(rgb(240, 10, 10), rgb(211, 126, 126));
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;}
  to {       background: -webkit-linear-gradient(rgb(0, 162, 255), rgb(211, 126, 126));
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;} 


}*/
@keyframes background_grad_text{

  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}


.main_div1{
  width: 90%;
  min-height: 50vh;
  display: flex;
  justify-content: center;
  margin: auto;
  border-radius: 20px;
  border-width: 0px;
  background-color: rgba(255, 255, 255, 0.438);
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  /*defingign the 4*3 grid layout to store the all the entry boxex*/



}

#main_form{
  width: 100%;
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  grid-template-rows: 33% 33% 33%;
}



input[type="text"]{
  border-radius: 15px;
  border-width: 0px;
  padding: 5px;
  margin: 20px;
  font-size: larger;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
}

.radio_div{
  width: 100%;
  grid-column: 3/5;
  justify-content: center;
  margin: auto;
  align-items: center;
  text-align: center;
  font-size: larger;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 100%;
}

input[type='radio']{
  margin: 50px;
}

select{
  border-radius: 15px;
  border-width: 0px;
  padding: 5px;
  margin: 20px;
  font-size: larger;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
}

